<template>
  <div
      class="m-search-bar"
      :class="$route.name == 'view' ? 'can-return' : ''"
  >
    <div class="m-return">
      <el-button class="u-return-btn" @click="return_handle"
      >
      </el-button
      >
    </div>
    <div class="m-search">
      <el-input
          class="u-search-input"
          v-model="keyword"
          @keydown.enter.native="search_handle"
          placeholder=""
      >
        <span slot="prepend"></span>
      </el-input>
      <el-button
          class="u-search-btn"
          type="primary"
          plain
          @click="search_handle"
      >
      </el-button
      >
    </div>
  </div>
</template>

<script>
  export default {
    name: "Search",
    data() {
      return {
        keyword: "",
      };
    },
    methods: {
      return_handle() {
        history.back();
      },
      search_handle() {
        let params = {};
        // 
        if (this.$store.state.sidebar.tag) params.tag = this.$store.state.sidebar.tag;

        this.$router.push({
          name: "normal",
          params: params,
          query: {keyword: this.keyword, page: 1},
        });
      },
    },
  };
</script>

<style lang="less">
  @import "../assets/css/components/search.less";
</style>
